h2 {
  border-bottom: 2px dotted #faaca8;
}

.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px #333;
  margin: 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background: white;
  align-items: center;
  width: 22%;
  max-width: 25%;
  padding: 15px;
}

.image {
  border-radius: 50%;
  width: 300px;
  height: auto;
  max-width: 80%;
  object-fit: cover;
  margin: 10px 0 20px 0;
  box-shadow: 0px 3px 8px #666;
}

@media screen and (max-width: 640px) {
  .recipe {
    min-width: 70%;
    max-height: 500px;
    overflow: scroll;
  }
}

@media screen and (min-width: 641px) and (max-width: 800px) {
  .recipe {
    min-width: 35%;
    max-height: 500px;
    overflow: scroll;
    padding: 20px 30px;
    margin: 10px 5px;
  }
}

@media screen and  (min-width: 801px) and (max-width: 1024px) {
  .recipe {
    min-width: 35%;
    max-height: 600px;
    overflow: scroll;
  }
}
