.App {
  min-height: 100vh;
  background-image: linear-gradient(-20deg, #ddd6f3 0%, #faaca8 100%, #faaca8 100%);
  font-family: 'Inter', 'Helvetica', 'Arial', sans-serif;
  padding: 0 20px;
}

.search-form {
  padding-top: 20px;
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 40%;
  /* height: 20px; */
  border: none;
  padding: 15px;
  color: #666;
  font-size: 1rem;
}

.search-button {
  background-color: #fb7b75;
  border: none;
  height: 50px;
  padding: 5px 20px;
  color: white;
  font-size: 1rem;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

input.large { display: inline-block; }
input.small { display: none; }

@media (max-width: 399px) {
  input.large { display: none; }
  input.small { display: inline-block; }
}
